/* xs */

@media only screen and (max-width: 575px) {
    .hide-xs {
        display: none;
    }
    .show-xs {
        display: block;
    }
}

/* sm */

@media only screen and (min-width: 576px) {
    .hide-sm {
        display: none;
    }
    .show-sm {
        display: block;
    }
}

/* md */

@media only screen and (min-width: 768px) {
    .hide-md {
        display: none;
    }
    .show-md {
        display: block;
    }
}

/* lg */

@media only screen and (min-width: 992px) {
    .hide-lg {
        display: none;
    }
    .show-lg {
        display: block;
    }
}

/* xl */

@media only screen and (min-width: 1200px) {
    .hide-xl {
        display: none;
    }
    .show-xl {
        display: block;
    }
}

.noselect {
    -webkit-touch-callout: none;
    /* iOS Safari */
    -webkit-user-select: none;
    /* Safari */
    -khtml-user-select: none;
    /* Konqueror HTML */
    -moz-user-select: none;
    /* Old versions of Firefox */
    -ms-user-select: none;
    /* Internet Explorer/Edge */
    user-select: none;
    /* Non-prefixed version, currently
                                    supported by Chrome, Opera and Firefox */
}

.display-inline {
    display: inline;
}

.display-inline-block {
    display: inline-block;
}

.display-block {
    display: block;
}

.display-none {
    display: none;
}

.left-0 {
    left: 0 !important;
}

.right-0 {
    right: 0 !important;
}
.right-50 {
    right: 50% !important;
}


.bottom-0 {
    bottom: 0 !important;
}

.position-absolute {
    position: absolute !important;
}

.position-relative {
    position: relative !important;
}

.text-white {
    color: #fff !important;
}

.unset{
    all: unset !important;
}