	z-index: 9999;
	transition: all .3s ease;
	padding: 0;
	background-color: rgba(0, 0, 0, .8);
	opacity: 0
}

.mobile-menu.active {
	height: 100%;
	width: 100%;
	top: 0;
	opacity: 1
}

.mobile-menu .mobile-menu__wrapper {
	height: 100%;
	overflow: auto;
	font-size: 14px;
